@keyframes entranceByRight {
	0% {
		opacity: 0;
		transform: translateX(250px);
	}

	60% {
		opacity: 1;
		transform: translateX(-10px);
	}

	100% {
		opacity: 1;
		transform: translateX(0);
	}
}



@keyframes entranceByZoom {
	0% {
		transform: scale(0);
	}

	100% {
		transform: scale(1);
	}
}

/* animation: entranceByZoom 1s ease 0s 1 normal forwards; */

